<template>
  <div class="CooperationCase">
    <h1>合作案例</h1>
    <div class="comment">
      <el-row :gutter="20">
        <el-col :span="8">
          <div class="grid-content">
            <img src="../assets/video/1.png" @click="open(0)" />
          </div>
          <div class="grid-content">
            <img src="../assets/video/2.png" @click="open(3)"/>          
          </div>
          <div class="grid-content1">
            <img src="../assets/video/3.png" @click="open(2)"/>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content1">
            <img src="../assets/video/4.png" @click="open(1)" />          
          </div>
          <div class="grid-content">
            <img src="../assets/video/5.png" @click="open(4)"/>
          </div>
          <div class="grid-content">
            <img src="../assets/video/6.png" @click="open(8)"/>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content">
            <img src="../assets/video/7.png" @click="open(5)"/>
          </div>
          <div class="grid-content1">
            <img src="../assets/video/8.png" @click="open(6)"/>
          </div>          
          <div class="grid-content">
            <img src="../assets/video/9.jpg" @click="open(7)"/>          
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog :visible.sync="dialogTableVisible" size="ful" :before-close="close">
      <div class="dialog">
        <h1>{{title}}</h1>
        <iframe height=500 width=800 :src="video" frameborder=0></iframe>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Row, Col, Dialog } from 'element-ui'

export default{
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      dialogTableVisible: false,
      title: '',
      video: '',
      videolist: [
        {
          title: '五位明星主播首次合体开拍雪碧mv',
          video: 'http://player.youku.com/embed/XMjg5Njk3MTExMg=='
        },
        {
          title: '电竞高材生2009开课至慧学堂',
          video: 'http://player.youku.com/embed/XMjg5Njk2MTUyNA=='
        },
        {
          title: '宝马X1与电竞王者2009一起玩出道',
          video: 'http://player.youku.com/embed/XMjg5Njk2OTY4OA=='
        },
        {
          title: '被炉石耽误的狼人杀主播囚徒喝康师傅配火锅',
          video: 'http://player.youku.com/embed/XMjg5NzAwMjEyMA=='
        },
        {
          title: '电竞王者2009端午请你吃湾仔码头',
          video: 'http://player.youku.com/embed/XMjg5NzM4MzI1Ng=='
        },
        {
          title: '首次集结各大平台顶级主播开启雪碧团战之夜',
          video: 'http://player.youku.com/embed/XMjg5NzQ0MjI0OA=='
        },
        {
          title: '小苍必胜宅乐饕饕',
          video: 'http://player.youku.com/embed/XMjg5OTY0MDk3Ng=='
        },
        {
          title: '雪佛兰迈锐宝保镖之夜',
          video: 'http://player.youku.com/embed/XMjg5OTMzNDc3Mg=='
        },
        {
          title: '迪拉克全国7站路演首次直播进行',
          video: 'http://player.youku.com/embed/XMjg5NzM5MDY2OA=='
        }
      ]
    }
  },
  methods: {
    open (n) {
      this.title = this.videolist[n].title
      this.video = this.videolist[n].video
      this.dialogTableVisible = true
    },
    close (done) {
      this.video = ''
      done()
    }
  }
}
</script>

<style lang="less" rel="stylesheet/less" >
.el-dialog__wrapper{
  div{
    height: auto;
  }
}
</style>
<style lang="less" rel="stylesheet/less" scoped>
.CooperationCase {
  height: auto;
  width: 100%;
  overflow: hidden;
  margin: 100px auto 0;
  padding: 100px 0;
  background: #edeeef;
  h1 {
    text-align: center;
    margin-bottom: 100px;
  }
  .comment {
    width: 1000px;
    height: auto;
    margin: 50px auto;
    .grid-content{
      height:270px;
      overflow: hidden;
      background: Silver;
      margin-bottom: 20px;
      position: relative;      
      img{
        height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        -webkit-filter:grayscale(1);
      }
    }
    .grid-content1{
      height: 560px;
      overflow: hidden;
      margin-bottom: 20px;
      background: Gray;
      position: relative;
      img{
        height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        -webkit-filter:grayscale(1);
      }
    }
    img:hover{
      -webkit-filter:grayscale(0);
    }
  }
}

.dialog{
  height: 550px;
  width: 1000px;
  text-align: center;
  h1{
    margin: 0;
  }
}
</style>



