<template>
  <div>
    <el-carousel height="100%" arrow="never">
      <el-carousel-item key="1">
      <img src="../assets/barr.jpg" :class="{img2: !getsb}"/>
      </el-carousel-item>
      <el-carousel-item key="2">
      <img src="../assets/barr2.jpg" :class="{img2: !getsb}"/>
      </el-carousel-item>
      <el-carousel-item key="3">
      <img src="../assets/barr3.jpg" :class="[{img1: !getsb},{img2: !getsb}]"/>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import { Carousel, CarouselItem } from 'element-ui'

export default {
  components: {
    [Carousel.name]: Carousel,
    [CarouselItem.name]: CarouselItem
  },
  computed: {
    getsb () {
      if (navigator.userAgent.match(/(iPhone|iPod|Android|ios)/i)) {
        return false
      }
      return true
    }
  }
}
</script>

<style lang="less" rel="stylesheet/less" scoped>
img{
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)
}
.img1{
  transform: translateX(0);
  left: -400px;
}
.img2{
  height: 100%;
}
</style>



